@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";

  font-display: swap;
  src:
    url("../fonts/.woff2") format("woff2"),
    url("../fonts/.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'PT Mono';

  font-display: swap;
  src: url("../../fonts/pt-mono-v13-latin_cyrillic-regular.woff2") format("woff2"), url("../../fonts/pt-mono-v13-latin_cyrillic-regular.woff") format("woff");
}

