  font-display: swap;
  src: url("../../fonts/roboto-condensed-v25-latin_cyrillic-700.woff2") format("woff2"), url("../../fonts/roboto-condensed-v25-latin_cyrillic-700.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: 'Roboto Condensed';

  font-display: swap;
